import cmdCellItem from '@/components/cmd-cell-item/cmd-cell-item.vue'
export default {
	components: {
		cmdCellItem
	},
	data() {
		return {
			pages: 1,
			showDelMask: false,
			showMask: false,
			radioList: [],

			list: [
				// 	{ //历史群发记录
				// 	date: '2019-10-15  12:20', //发布的时间
				// 	msContent: 'asjdhasjfhasbf卡机的办卡积分来看就爱上了开发', //发布的内容
				// 	itemMan: ['哈偶像', '安师大', '萨达是', '萨达', '啥就后端是高分', '撒娇的灰色返回', '埃及食物'] //用户名称数组
				// },
			]
		};
	},
	onLoad(options) {
		this.pages = options.pages
		if (this.pages == 2) {
			// uni.setNavigationBarTitle({
			// 	title: '群发助手'
			// })
			this.getData()

		}

	},
	onShow() {
		this.getData()
	},
	methods: {
		onProviewImg(img) {
			let imgArr = []
			imgArr.push(img)
			uni.previewImage({
				urls: imgArr,
				// longPressActions: {
				//     itemList: ['发送给朋友', '保存图片', '收藏'],
				//     success: function(data) {
				//         console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
				//     },
				//     fail: function(err) {
				//         console.log(err.errMsg);
				//     }
				// }
			});
		},
		getData() {
			this.$util.get('/wechatUser/massSendInfoList').then(res => {
				this.list = res.data.data
			})
			this.$util.get('/wechatUser/kzList').then(res => {
				res.data.data.forEach(item => {
					item.checked = false
				})
				this.radioList = res.data.data
			})

		},
		onshowhistoryList() {
			uni.navigateTo({
				url: '/pages/showSet/groupAssistant/groupAssistant?pages=2'
			})
		},
		delItem(item) {
			console.log(item, '11')
			this.listItem = item
			this.showDelMask = true
		},
		colseMask() {
			setTimeout(() => {
				this.showDelMask = false
			}, 1000)
		},
		onPublish() {
			this.$util.get('/wechatUser/massSendInfoRandom?kzNum=' + this.selectSarkName).then(res => {
				if (res.data.code == 200) {
					uni.showToast({
						title: '群发成功',
					})
					this.showMask = false
					this.getData()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
		tapMask() {
			this.showMask = false
		},
		changRadio(item) {
			this.radioList.forEach((item, index) => {
				this.$set(item, 'checked', false)
			})
			this.$set(item, 'checked', true)
			console.log(item, '444');
			this.selectSarkName = item.value
			// item.checked = !item.checked

		},
		onDelList() {

			this.$util.get('/wechatUser/massSendInfoDel?msId=' + this.listItem.id).then(res => {
				if (res.data.code == 200) {
					this.showDelMask = false
					this.getData()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
		onshowNew(type, item) {
			console.log(item, 'item')
			if (type == 1) {
				uni.navigateTo({
					url: '/pages/showSet/editNew/editNew?userInfo=' + JSON.stringify(item),
				})
			} else {
				// uni.navigateTo({
				// 	url: '/pages/showSet/friendsList/friendsList'
				// })
				this.showMask = true

			}

		}
	}
}
